import { getActClass } from './utils';
import PressQRCode from '../press-q-r-code/press-q-r-code';


export default {
  name: 'PressQRCodePopup',
  components: {
    PressQRCode,
  },
  props: {
    qrCodeUrl: {
      type: String,
      default: '',
      require: true,
    },
    type: {
      type: Number, // 1:QQ, 2:微信
      default: 2,
      require: false,
    },
    title: {
      type: String,
      default: '',
    },
    description: {
      type: String,
      default: '截图或长按保存图片',
    },
    tip: {
      type: String,
      default: '',
    },
    useTipClass: {
      type: Boolean,
      default: false,
    },
  },
  emits: [
    'onCancel',
  ],
  data() {
    return {
      isShowPopup: true,
    };
  },
  computed: {
    typeName() {
      return this.type === 1 ? 'QQ' : '微信';
    },
    innerTitle() {
      const { typeName, title } = this;
      if (title) {
        return title;
      }
      return `请使用${typeName}扫码打开活动`;
    },
    innerTip() {
      const { typeName, tip } = this;
      if (tip) {
        return tip;
      }
      return `使用${typeName}扫码打开访问页面`;
    },
  },
  methods: {
    clickCancel() {
      this.isShowPopup = false;
      this.timer = setTimeout(() => {
        this.$emit('onCancel');
      }, 400);
    },
    getActClass(...args) {
      return getActClass(this.useTipClass, args);
    },

  },
};


